.footer-outer {
    height: fit-content;
    margin: 15% 0px 20px 0px;
    display: flex;
    width: 90vw;
    margin-left: auto;
    margin-right: auto;
}

.footer-text {
    font-family: "asdf";
    font-size: 1.7vw;
    margin-left: 0px;
    width: 100%;
}

.footer-contact {
    display: flex;
    width: 100%;
}

.footer-contact-text {
    font-size: 14px;
    font-family: "asdf";
    color: black;
    text-decoration: none;
}

.linked-in:hover {
    cursor:pointer; 
}

.column1 {
    width: 30vw;
    text-align: center;
    margin: 0;
}

.column2 {
    width: 30vw;
    text-align: center;
    margin: 0;
}

.column3 {
    width: 30vw;
    text-align: center;
}

@media screen and (max-width: 715px) {
    .footer-contact {
        display: block;
        text-align: center;
    }
    
    .column1 {
        width: 100%;
        margin-bottom: 20px;
    }

    .column2 {
        width: 100%;
        margin-bottom: 20px;
    }

    .column3 {
        width: 100%;
        margin-bottom: 20px;
    }
  }