.container {
    display: flex;
    flex-direction: column;
    padding: 39px 0px;
}

.off {
    background-color: var(--off-background);
}

.off-text {
    color: var(--off-text);
}

.title {
    font-family: 'Inter';
    font-size: 39px;
    margin-right: auto;
    margin-left: auto;
    margin-bottom: 39px;
    margin-top: 0px;
}

.contact-container {
    transform: rotate(-90deg);
    position: fixed;
    display: flex;
    bottom: 50vh;
}

.resource {
    font-family: 'EB Garamond';
    font-size: 15px;
    margin-right: 5px;
    margin-top: 0px;
}

.text {
    font-family: 'EB Garamond';
    font-size: 15px;
    margin: 3px 0px;
}

/* About & Education */

.halves-container {
    display: flex;
}

.first-half {
    width: 50vw;
    display: flex;
    flex-direction: column;
}

.first-half > p {
    margin-left: auto;
    margin-right: auto;
}

.second-half {
    width: 50vw;
    display: flex;
}

.display-circle {
    border-radius: 50%;
    background-color: var(--accent);
    width: 250px;
    height: 250px;
    margin-left: auto;
    margin-right: auto;
}

/* Experiences */

.expandable-container {
    width: 85vw;
    margin-left: auto;
    margin-right: auto;
    background-color: var(--fill);
    border-radius: 5px;
    margin-top: 7px;
    padding: 3px;
}

.expandable-container:hover {
    cursor: pointer;
    box-shadow: 2px 3px var(--accent);
}

/* Skills */

.skills {
    width: 85vw;
    height: 75vh;
    display: flex;
    flex-wrap: wrap;
    border: black 1px solid;
    margin-left: auto;
    margin-right: auto;
    padding: 4px;
}

.box {
    background-color: var(--fill);
    height: 100%;
    width: 100%;
    flex: 1;
}

.box-cover {
    padding: 4px;
    border: black 0px solid;
    display: flex;
    box-sizing: border-box;
}

.small {
    width: 20%;
    height: 20%;
}

.medium {
    width: 20%;
    height: 35%;
}

.large {
    width: 25%;
    height: 45%;
}